  /*
  =============== 
  Navbar
  ===============
  */
  nav {
    background: transparent;
    box-shadow: 0 5px 15px rgba(70, 70, 70, 0.226);
  
  }
  .nav-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
  }
  .nav-toggle {
    font-size: 1.5rem;
    color: hsl(205, 90%, 88%);
    background: transparent;
    border-color: transparent;
    transition: all 0.3s linear;
    cursor: pointer;
  }
  .nav-toggle:hover {
    color: hsl(205, 86%, 81%);
    transform: rotate(90deg);
  }
  /* .logo{
    height: 40px;
    cursor: pointer;
  } */

  .nav-header img {
    width: 170px;
  }

  .links a {
    color:hsl(210, 36%, 96%);
    font-size: 1rem;
    text-transform: capitalize;
    letter-spacing: 0.1rem;
    display: block;
    padding: 0.5rem 1rem;
    transition: all 0.3s linear;
  }
  .links a:hover {
    background: hsl(205, 86%, 17%);
    color: hsl(212, 33%, 89%);
    padding-left: 1.5rem;
  }
  .social-icons {
    display: none;
  }
  .links-container {
    height: 0;
    overflow: hidden;
    transition: all 0.3s linear;
  }
  .show-container {
    height: 10rem;
  }
  @media screen and (min-width: 800px) {
    .nav-center {
      max-width: 1170px;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 1rem;
    }
    .nav-header {
      padding: 0;
    }
    .nav-toggle {
      display: none;
    }
    .links-container {
      height: auto !important;
    }
    .links {
      display: flex;
    }
    .links a {
      padding: 0;
      margin: 0 0.5rem;
    }
    .links a:hover {
      padding: 0;
      background: transparent;
      color: rgb(250, 151, 168);
    }
    .social-icons {
      display: flex;
    }
    .social-icons a {
      margin: 0 0.5rem;
      color: hsl(205, 100%, 96%);
      font-size: 1.3rem;
      transition: all 0.3s linear;
    }
    .social-icons a:hover {
      color: rgb(255, 176, 189);
    }
  }

/* 
============
hero-section
============
*/

.section {
    padding: 5rem 0;
  }
  
  .section-center {
    width: 90vw;
    margin: 0 auto;
    max-width: 1170px;
  }

.hero {
    background-color: var(--background-color);
    margin-left: 2rem;
  }

.hero .underline {
    margin-bottom: .5erm;
    margin-left: 0;
}

.hero-img {
    display: none;
}

.hero-center {
    min-height: calc(100vh - 5rem);
    display: grid;
    place-items: center;
}

.hero-btn {
    margin-top: 1.25rem;
    margin-right: .8rem;
}

.hero-info h1 {
    color: #fff;
    line-height: 85px;
}

.hero-info .typing{
    color: rgb(153, 153, 153);
}

.typing::before {
    content: "";
    animation: text 15s step-end infinite;
}

@keyframes text {
    0% { content: ''; }
    20% { content: 'U'; }
    22% { content: 'UI'; }
    24% { content: 'UI/'; }
    26% { content: 'UI/U'; }
    28% { content: 'UI/UX'; }
    30% { content: 'UI/UX '; }
    32% { content: 'UI/UX D'; }
    34% { content: 'UI/UX De'; }
    36% { content: 'UI/UX Des'; }
    38% { content: 'UI/UX Desi'; }
    40% { content: 'UI/UX Desig'; }
    42% { content: 'UI/UX Design'; }
    44% { content: 'UI/UX Designe'; }
    46% { content: 'UI/UX designer'; }
    48% { content: 'UI/UX designer.'; }
    56% { content: ''; }
    58% { content: 'F'; }
    60% { content: 'Fr'; }
    62% { content: 'Fro'; }
    64% { content: 'Fron'; }
    66% { content: 'Front'; }
    68% { content: 'Front-'; }
    70% { content: 'Front-E'; }
    72% { content: 'Front-En'; }
    74% { content: 'Front-End'; }
    76% { content: 'Front-End '; }
    78% { content: 'Front-End D'; }
    80% { content: 'Front-End De'; }
    82% { content: 'Front-End Dev'; }
    84% { content: 'Front-End Deve'; }
    86% { content: 'Front-End Devel'; }
    88% { content: 'Front-End Develo'; }
    90% { content: 'Front-End Develop'; }
    92% { content: 'Front-End Develope'; }
    94% { content: 'Front-End Developer'; }
    96% { content: 'Front-End Developer.'; }
    100% { content: ''; }
    
}

.typing::after {
    content: '';
    display: inline-block;
    width: 0.1em; 
    height: 0.85em;
    margin-left: 0.1em;
    background-color: yellow;
    animation: blink 0.75s ease-in-out infinite;
}
    @keyframes blink {
      0%, 25%, 100% { opacity: 1; }
      50%, 75% { opacity: 0; }
}

@media screen and (min-width: 992px) {
    .hero-img {
        display: block;
        position: relative;
    }

    .hero-center {
        grid-template-columns: 1fr 1fr;
        padding-left: 6rem;
    }

    .hero-img::before,
    .about-img::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        /* border: .25rem solid #fff; */
        background-color: rgba(179, 179, 179, 0.733);
        /* top: 1rem; */
        right: -.5rem;
        transform: rotate(7deg);
        border-radius: var(--radius);
    }

    .hero-photo,
    .about-img {
        max-width: 25rem;
        max-height: 30rem;
        object-fit: cover;
        border-radius: var(--radius);
        position: relative;
      }
}

/* 
===========
About. 
===========
. */

.about-title h2{
    text-align: left;
    margin-bottom: 2rem;
    color: #fff;
  }
  .about-title .underline {
    margin-left: 0;
  }
  .about-info p{
      color: #fff;
  }
  .about-info h2{
    text-align: center;
  }

  .about-info .underline {
    margin: 0 auto;
  }
  .about-center {
    display: grid;
    grid-gap: 3rem 2rem;
    gap: 3rem 2rem;
  }
  .about-img {
    justify-self: center;
    max-width: 400px;
    min-width: 300px;
  }
  .about-img img {
      border-radius: var(--radius);
  }
  @media screen and (min-width: 992px) {
    .about-center {
      grid-template-columns: 1fr 1fr;
    }
    .about-img {
      position: relative;
      width: 350px;
    }
    .about-img::before {
      left: -.5rem;
      transform: rotate(-7deg);
      width: 100%;
      height: 100%;
      z-index: -1;
    }
    
    .about-info {
      align-self: center;
    }
    
    .about-info h2{
      text-align: left;
    }
  
    .about-info .underline {
      margin: 0;
    }
  }


  /* services */

.section-title h2 {
    color: #fff;
}
.service {
    padding: 3rem 1.5rem;
    margin-bottom: 2rem;
    border-radius: var(--radius);
    text-align: center;
    transition: var(--transition);
    color: #fff;
    border: 2px solid #fff;
    border-radius: var(--radius);
}

.service-icon {
    font-size: 2rem;
    margin-bottom: 1.5rem;
}

.servce .underline {
    width: 3rem;
    height: .12rem;
    transition: var(--transition);
}

.service p {
    transition: var(--transition);
}

.service:hover {
    background-color: rgb(145, 145, 145);
    border-radius: var(--radius);
    color: #111;
}

@media screen and (min-width: 676px) {
    .services-center {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 2rem;
        -webkit-column-gap: 2rem;
                column-gap: 2rem;
    }
}

@media screen and  (min-width: 992px) {
    .services-center {
        grid-template-columns: 1fr 1fr 1fr;
    }

    .service {
        border: 2px solid #fff;
        border-radius: var(--radius);
        color: #fff;
    }
}
@media screen and (min-width: 768px) {
    .skills-center {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 2rem;
        -webkit-column-gap: 2rem;
                column-gap: 2rem;
    }
}

.skills h3 {
    margin: 1.5rem 0;
    color: #fff;
}

.skill {
    margin-bottom: 1.25rem;
}

.skill p {
    margin-bottom: 0.5rem;
    text-transform: capitalize;
    color: rgb(197, 197, 197);
}

.skill-container {
    background: #fff;
    height: 1rem;
    width: 100%;
    border-radius: var(--radius);
    position: relative;
}

.skill-value {
    position: absolute;
    top: 0;
    left: 0;
    background: gray;
    height: 100%;
    width: 50%;
    border-radius: var(--radius);
}

.value-60 {
    width: 60%;
  }

  .value-70 {
    width: 70%;
  }

  .value-80 {
    width: 80%;
  }

  .skill-text {
    position: absolute;
    top: -2rem;
    left: 50%;
    transform: translateX(-50%);
  }

  .skill-text-60 {
    left: 60%;
  }

  .skill-text-70 {
    left: 70%;
  }
  
  .skill-text-80 {
    left: 80%;
  }

.card {
    height: 27rem;
    position: relative;
    perspective: 1500px;
}

.card-side {
    transition: all 2s linear;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: var(--radius);
    transform: translate3d(0, 0, 0);
    visibility: visible;
}

.card-front {
    background: rgba(63, 71, 87, 0.89);
}
.card-back {
    background: rgba(84, 96, 117, 0.801);
    transform: rotateY(-180deg);
    display: grid;
    place-items: center;
}

.card:hover .card-front {
    transform: rotateY(-180deg);
}

.card:hover .card-back {
    transform: rotateY(0);
}

.card-info {
    padding: 1rem 1.5rem;
    color: #fff;
}

.card-front img {
    height: 13rem;
    object-fit: cover;
    border-top-right-radius: var(--radius);
}

.card-footer {
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
}

.card-footer img {
    width: 2rem;
    height: 2rem;
    object-fit: cover;
    border-radius: 50%;
}

.card-footer p {
    margin-bottom: 0;
    text-transform: uppercase;
    justify-self: end;
    color: rgb(255, 255, 255);
    font-size: .85rem;
}

.blog-center {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(330px, 1fr));
    grid-gap: 3rem 1rem;
    gap: 3rem 1rem;
}

.blogs-btn-wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.5rem;  
}

.blogs-btn {
    text-decoration: none;
    width: 150px;
    color: #000;
    font-size: 1rem;
    background-color: #fff;
    border: 1px solid #fff;
    border-radius: 8px;
    padding: .5rem 1rem;
    text-align: center;
}
.blogs-btn:hover {
    border: 1px solid #fff;
    background: none;
    color: #fff;  
}
.footer {
    border-top: 1px solid rgb(92, 92, 92);
    padding-bottom: 1.6rem;
    background: transparent;
    display: grid;
    place-items: center;
}

.footer .icons {
    margin: 1rem 0;
    display: flex;
}

.footer .icon{
    padding: 1rem;
    color: #fff;
    font-size: 1.5rem;
}

.footer .icon:hover {
    color: pink;
}

.footer p {
    font-size: 1.2rem;
    text-transform: capitalize;
    color: #fff;
}

.projects-text {
    width: 85vw;
    max-width: 30rem;
    margin: 0 auto;
    color: #fff;
  }
  .project {
    position: relative;
    background: rgba(103, 0, 143, 0.493);
    border-radius: var(--radius);
    margin-bottom: 2rem;
  }
  .project-info {
    text-align: center;
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.3s ease-in-out;
    opacity: 0;
  }
  .project:hover .project-info {
    opacity: 1;
  }
  .project-img {
    transition: all 0.3s ease-in-out;
    border-radius: var(--radius);
    height: 15rem;
    object-fit: cover;
  }
  .project:hover .project-img {
    opacity: 0.1;
  }
  .project::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0.25rem solid #fff;
    border-radius: var(--radius);
    transition: all 0.3s ease-in-out;
    opacity: 0;
  }
  .project:hover::after {
    opacity: 1;
    transform: scale(0.8);
  }

  .project-btn-wrapper {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 1.5rem;  
  }

  .project-btn {
      text-decoration: none;
      width: 150px;
      color: #fff;
      font-size: 1rem;
      border: 1px solid #fff;
      border-radius: 8px;
      padding: .5rem 1rem;
      text-align: center;
  }
  .project-btn:hover {
      background-color: #fff;
      color: #000;  
  }

  @media screen and (min-width: 676px) {
    .projects-center {
      display: grid;
      grid-template-columns: 1fr 1fr;
      -webkit-column-gap: 2rem;
      -moz-column-gap: 2rem;
      grid-column-gap: 2rem;
      column-gap: 2rem;
    }
  }
  @media screen and (min-width: 992px) {
    .projects-center {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  @media screen and (min-width: 1170px) {
    .projects-center {
      grid-template-rows: 200px 200px;
      grid-gap: 1rem;
      gap: 1rem;
      grid-template-areas:
        "a b b"
        "a c d";
    }
    .project-img {
      height: 100%;
    }
    .project {
      height: 100%;
    }
    .project-1 {
      grid-area: a;
    }
    .project-2 {
      grid-area: b;
    }
    .project-3 {
      grid-area: c;
    }
    .project-4 {
      grid-area: d;
    }
  }
