/* 
============
hero-section
============
*/

.section {
    padding: 5rem 0;
  }
  
  .section-center {
    width: 90vw;
    margin: 0 auto;
    max-width: 1170px;
  }

.hero {
    background-color: var(--background-color);
    margin-left: 2rem;
  }

.hero .underline {
    margin-bottom: .5erm;
    margin-left: 0;
}

.hero-img {
    display: none;
}

.hero-center {
    min-height: calc(100vh - 5rem);
    display: grid;
    place-items: center;
}

.hero-btn {
    margin-top: 1.25rem;
    margin-right: .8rem;
}

.hero-info h1 {
    color: #fff;
    line-height: 85px;
}

.hero-info .typing{
    color: rgb(153, 153, 153);
}

.typing::before {
    content: "";
    animation: text 15s step-end infinite;
}

@keyframes text {
    0% { content: ''; }
    20% { content: 'U'; }
    22% { content: 'UI'; }
    24% { content: 'UI/'; }
    26% { content: 'UI/U'; }
    28% { content: 'UI/UX'; }
    30% { content: 'UI/UX '; }
    32% { content: 'UI/UX D'; }
    34% { content: 'UI/UX De'; }
    36% { content: 'UI/UX Des'; }
    38% { content: 'UI/UX Desi'; }
    40% { content: 'UI/UX Desig'; }
    42% { content: 'UI/UX Design'; }
    44% { content: 'UI/UX Designe'; }
    46% { content: 'UI/UX designer'; }
    48% { content: 'UI/UX designer.'; }
    56% { content: ''; }
    58% { content: 'F'; }
    60% { content: 'Fr'; }
    62% { content: 'Fro'; }
    64% { content: 'Fron'; }
    66% { content: 'Front'; }
    68% { content: 'Front-'; }
    70% { content: 'Front-E'; }
    72% { content: 'Front-En'; }
    74% { content: 'Front-End'; }
    76% { content: 'Front-End '; }
    78% { content: 'Front-End D'; }
    80% { content: 'Front-End De'; }
    82% { content: 'Front-End Dev'; }
    84% { content: 'Front-End Deve'; }
    86% { content: 'Front-End Devel'; }
    88% { content: 'Front-End Develo'; }
    90% { content: 'Front-End Develop'; }
    92% { content: 'Front-End Develope'; }
    94% { content: 'Front-End Developer'; }
    96% { content: 'Front-End Developer.'; }
    100% { content: ''; }
    
}

.typing::after {
    content: '';
    display: inline-block;
    width: 0.1em; 
    height: 0.85em;
    margin-left: 0.1em;
    background-color: yellow;
    animation: blink 0.75s ease-in-out infinite;
}
    @keyframes blink {
      0%, 25%, 100% { opacity: 1; }
      50%, 75% { opacity: 0; }
}

@media screen and (min-width: 992px) {
    .hero-img {
        display: block;
        position: relative;
    }

    .hero-center {
        grid-template-columns: 1fr 1fr;
        padding-left: 6rem;
    }

    .hero-img::before,
    .about-img::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        /* border: .25rem solid #fff; */
        background-color: rgba(179, 179, 179, 0.733);
        /* top: 1rem; */
        right: -.5rem;
        transform: rotate(7deg);
        border-radius: var(--radius);
    }

    .hero-photo,
    .about-img {
        max-width: 25rem;
        max-height: 30rem;
        -o-object-fit: cover;
        object-fit: cover;
        border-radius: var(--radius);
        position: relative;
      }
}

/* 
===========
About. 
===========
. */

.about-title h2{
    text-align: left;
    margin-bottom: 2rem;
    color: #fff;
  }
  .about-title .underline {
    margin-left: 0;
  }
  .about-info p{
      color: #fff;
  }
  .about-info h2{
    text-align: center;
  }

  .about-info .underline {
    margin: 0 auto;
  }
  .about-center {
    display: grid;
    gap: 3rem 2rem;
  }
  .about-img {
    justify-self: center;
    max-width: 400px;
    min-width: 300px;
  }
  .about-img img {
      border-radius: var(--radius);
  }
  @media screen and (min-width: 992px) {
    .about-center {
      grid-template-columns: 1fr 1fr;
    }
    .about-img {
      position: relative;
      width: 350px;
    }
    .about-img::before {
      left: -.5rem;
      transform: rotate(-7deg);
      width: 100%;
      height: 100%;
      z-index: -1;
    }
    
    .about-info {
      align-self: center;
    }
    
    .about-info h2{
      text-align: left;
    }
  
    .about-info .underline {
      margin: 0;
    }
  }